import styled from "styled-components";

export const GenContainer = styled("section")`
  position: relative;
  width: 100%;
  height: 410px;
  padding: 0rem;
  margin: 0rem;
  background: #111;
`;

export const BlockContainer = styled("section")`
  position: relative;
  width: 100%;
  height: 100%;
  background-image: linear-gradient( rgba(100, 1, 1, 0.2), rgba(1, 1, 1, 0.2) );

`;

export const ContentWrapperTitle = styled("div")`
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
  height: 100%;
`;

export const ContentWrapper = styled("div")`
  position: absolute;
  margin: 0rem;
  padding: 5px 0;
`;

export const Slider = styled("p")`
  animation: scroll 20s linear infinite;
  display: flex;
  margin: 0rem;
  padding: 0rem;

  @keyframes scroll {
    0% { transform: translateX(0); }
    100% { transform: translateX(calc(-405px * 7))}
  }
`;

export const SliderImg = styled("div")`
  padding: 0px 5px 5px 0px;
`;

export const TextTitle = styled("section")`
  color: #FFF;
  font-family: BalooBold;
  font-size: 64px;
  line-height: 50px;
  margin: 0 0 3rem 0;
  text-shadow: 0 4px 4px rgba(81, 21, 0, 25%);

  @media only screen and (max-width: 768px) {
    font-size: 54px;
    margin: 0 0 1rem 0;
  }
`;

export const Text = styled("section")`
  color: #EEE;
  font-family: BalooMedium;
  font-size: 30px;
  line-height: 35px;
  max-width: 1000px;

  @media only screen and (max-width: 768px) {
    font-size: 26px;
  }
`;